 36px;\n\n    svg {\n      width: 16px;\n      height: 16px;\n      vertical-align: unset;\n    }\n\n    @include respond-to(md) {\n      height: 44px;\n      svg {\n        width: 25px;\n        height: 25px;\n\n      }\n    }\n\n    &:hover {\n      background: $color-dark;\n      opacity: 0.8;\n\n    }\n  }\n\n  .next-slide {\n    bottom: 24px;\n    left: 68px;\n\n    @include respond-to(md) {\n      left: 92px;\n    }\n  }\n\n  .prev-slide {\n    bottom: 24px;\n    left: 24px;\n  }\n}\n\nnav {\n  margin-top: 24px;\n  @include respond-to(md) {\n    margin-top: 0;\n  }\n\n  .breadcrumb {\n    font-size: 14px !important;\n    line-height: 16px;\n  }\n\n  .breadcrumb-item {\n    word-wrap: break-word !important;\n    width: fit-content;\n\n    > * {\n      white-space: normal;\n      width: fit-content;\n    }\n  }\n}\n\n.name {\n  font-weight: 500;\n  font-size: 18px;\n  line-height: 21px;\n\n  color: $color-dark;\n\n  @include respond-to(md) {\n    font-size: 24px;\n    line-height: 28px;\n  }\n\n  @include respond-to(lg) {\n    font-size: 32px;\n    line-height: 38px;\n  }\n}\n.description {\n  font-size: 18px !important;\n  line-height: 21px !important;\n  color: $color-gray !important;\n  margin-top: 16px;\n  * {\n    font-size: 18px !important;\n    line-height: 21px !important;\n    color: $color-gray !important;\n  }\n}\n\n.cost {\n  font-weight: 500;\n  font-size: 18px;\n  line-height: 21px;\n  color: $color-dark;\n\n  @include respond-to(md) {\n    font-size: 20px;\n    line-height: 28px;\n  }\n\n  @include respond-to(lg) {\n    font-size: 32px;\n    line-height: 38px;\n  }\n}\n\n.cost-wrapper {\n  margin-top: 24px;\n  button {\n    margin-top: 24px;\n\n    svg {\n     width: 14px;\n      height: 14px;\n      margin-left: 14px;\n    }\n\n    @include respond-to(sm) {\n      margin-top: 0;\n    }\n  }\n}\n\n.btn-cart {\n  background: $color-orange;\n  color: $color-dark;\n\n  font-weight: normal;\n  font-size: 16px;\n  line-height: 19px;\n  padding: 13px;\n  width: 100%;\n  border-radius: 16px 0;\n  transition: 0.3s;\n\n  &:hover {\n    background: $color-dark;\n    color: #FFFFFF;\n  }\n\n  &:disabled {\n    background: $color-dark;\n    opacity: 1;\n    color: #FFFFFF;\n    cursor: default;\n  }\n\n  @include respond-to(sm) {\n    width: auto;\n    padding: 10px 20px;\n  }\n}\n\n.attributes-row {\n  margin-top: 40px;\n\n  @include respond-to(lg) {\n    margin-top: 80px;\n  }\n\n  .attribute {\n    margin-top: 16px;\n  }\n\n  hr {\n    margin: 0;\n  }\n\n  h3 {\n    font-weight: 500;\n    font-size: 24px;\n    line-height: 28px;\n    color: $color-dark;\n  }\n\n  .attribute-name {\n    font-weight: normal;\n    font-size: 18px;\n    line-height: 21px;\n    color: $color-gray;\n  }\n\n  .attribute-value {\n    font-weight: normal;\n    font-size: 18px;\n    line-height: 21px;\n    color: $color-dark;\n  }\n\n\n}\n","$color-orange: #F4B12F;\r\n$color-dark: #1A1819;\r\n$color-gray: #767676;\r\n$color-light-gray: #F1F1F1;","@import 'colors';\r\n\r\n$container-max-widths: (\r\n        sm: 100%,\r\n        md: 740px,\r\n        lg: 1080px,\r\n        xl: 1280px,\r\n);\r\n$grid-breakpoints: (\r\n        xs: 0,\r\n        sm: 576px,\r\n        md: 767px,\r\n        lg: 1200px,\r\n        xl: 1365px,\r\n);\r\n\r\n$font-family-base: 'Rubik', sans-serif;\r\n$breadcrumb-item-padding-x: 0.1rem;\r\n$breadcrumb-active-color: $color-orange;\r\n$breadcrumb-divider-color: $color-gray;\r\n\r\n/// Mixin to manage responsive breakpoints\r\n/// @author Fulliton\r\n/// @param {String} $breakpoint - Breakpoint name\r\n/// @require $breakpoints\r\n@mixin respond-to($breakpoint) {\r\n  // If the key exists in the map\r\n  @if map-has-key($grid-breakpoints, $breakpoint) {\r\n    // Prints a media query based on the value\r\n    @media (min-width: map-get($grid-breakpoints, $breakpoint)) {\r\n      @content;\r\n    }\r\n  }\r\n\r\n    // If the key doesn't exist in the map\r\n  @else {\r\n    @warn \"Unfortunately, no value could be retrieved from `#{$breakpoint}`. \"\r\n        + \"Available breakpoints are: #{map-keys($grid-breakpoints)}.\";\r\n  }\r\n}\r\n\r\n@mixin respond-up($breakpoint) {\r\n  // If the key exists in the map\r\n  @if map-has-key($grid-breakpoints, $breakpoint) {\r\n    // Prints a media query based on the value\r\n    @media (max-width: map-get($grid-breakpoints, $breakpoint)) {\r\n      @content;\r\n    }\r\n  }\r\n\r\n    // If the key doesn't exist in the map\r\n  @else {\r\n    @warn \"Unfortunately, no value could be retrieved from `#{$breakpoint}`. \"\r\n        + \"Available breakpoints are: #{map-keys($grid-breakpoints)}.\";\r\n  }\r\n}"],"sourceRoot":""}]);
